import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { HomeService } from '@app/core/services/home.service';
import { ActionLabel, Label } from '@app/enum/app_enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-payment-list',
  templateUrl: './payment-list.component.html',
  styleUrls: ['./payment-list.component.css']
})
export class PaymentListComponent implements OnInit {
  @Input() valueChanges;
  @Output() onEditEmit: any = new EventEmitter<any>();

  public subscription: Subscription;
  public activeIndex: any;
  public paymentLists: any = [];
  public totalCount: any;
  public tableHeader = [Label.createDate, Label.payAccountType, Label.status, Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.delete, ActionLabel.view];
  public objectLabel: any = ["createdDate", "payAccountType", "status"];
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public searchData: any;
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public selectedPayment: any = {};
  public isCreate: boolean = false;
  public actionLinks: any = {
    editLink: "",
    deleteLink: "payments",
    idKeyname: "paymentId",
    isModal: true,
  }

  constructor(private _service: HomeService) { }

  ngOnInit(): void {
    this.getPaymentLists();
  }


  getPaymentLists() {
    this._service.paymentLists(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection,"payments")
      .subscribe((res) => {
        this.paymentLists.data = res.payments;
        this.totalCount = this.paymentLists.data.length;
        this.paymentLists.btnLink = this.actionLinks;
        this.paymentLists.tableHeader = this.tableHeader;
        this.paymentLists.actionBtns = this.actionBtns;
        this.paymentLists.objectLabel = ["createdDate", "payAccountType", "status"];
      });

  }

  pageChanges(e: any) {
    this.page = e;
    this.getPaymentLists();
  }

  onFilter(filter: {}) {
    this.filter = filter;
    this.getPaymentLists();
  }

  onsearch(val) {
    this.searchData = val;
    this.getPaymentLists();
  }

  onEdit(data) {
    console.log(data);
    this.onEditEmit.emit(data);
  }

  ngOnChanges(changes) {
    // changes.prop contains the old and the new value...
    console.log(changes);
    this.getPaymentLists();
    this.searchData = "";
    this.page = 1;
    this.limit = 10;
  }
  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
