import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";

@Injectable({
  providedIn: 'root'
})
export class HomeService {
  private baseUrl = environment.apiUrl;
  constructor(private http: HttpClient) { }

  paymentLists(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String,type:String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/"+type+"?search=" + searchData;
    else
      url =
        this.baseUrl +
        "/"+type+"?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  createPayment(payment,type) {
    let url = this.baseUrl + "/"+type;
    return this.http.post(url, payment)
  }

  editPayment(payment, paymentId,type) {
    let url = this.baseUrl + "/"+type+"/" + paymentId;
    return this.http.put(url, payment)
  }

  getPayemntDetail(id: String,type:String) {
    let url = this.baseUrl + "/"+type+"/" + id;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }
}
