import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { HomeService } from '@app/core/services/home.service';
import { ActionLabel, Label } from '@app/enum/app_enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-account-list',
  templateUrl: './account-list.component.html',
  styleUrls: ['./account-list.component.css']
})
export class AccountListComponent implements OnInit,OnDestroy {
  @Input() valueChanges;
  @Output() onEditEmit: any = new EventEmitter<any>();

  public subscription: Subscription;
  public activeIndex: any;
  public paymentLists: any = [];
  public totalCount: any;
  public tableHeader = [Label.name, Label.accountType, Label.amount, Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.delete, ActionLabel.view];
  public objectLabel: any = ["name", "accountType","amount"];
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public searchData: any;
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public selectedPayment: any = {};
  public isCreate: boolean = false;
  public actionLinks: any = {
    editLink: "",
    deleteLink: "accounts",
    idKeyname: "accountId",
    isModal: true,
  }

  constructor(private _service: HomeService) { }

  ngOnInit(): void {
    this.getPaymentLists();
  }


  getPaymentLists() {
    this._service.paymentLists(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection,"accounts")
      .subscribe((res) => {
        this.paymentLists.data = res.accounts;
        this.totalCount = this.paymentLists.data.length;
        this.paymentLists.btnLink = this.actionLinks;
        this.paymentLists.tableHeader = this.tableHeader;
        this.paymentLists.actionBtns = this.actionBtns;
        this.paymentLists.objectLabel =["name", "accountType","amount"];
      });
  }

  pageChanges(e: any) {
    this.page = e;
    this.getPaymentLists();
  }

  onFilter(filter: {}) {
    this.filter = filter;
    this.getPaymentLists();
  }

  onsearch(val) {
    this.searchData = val;
    this.getPaymentLists();
  }

  onEdit(data) {
    console.log(data);
    this.onEditEmit.emit(data);
  }

  ngOnChanges(changes) {
    // changes.prop contains the old and the new value...
    console.log(changes);
    this.getPaymentLists();
    this.searchData = "";
    this.page = 1;
    this.limit = 10;
  }
  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
