import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HomeService } from '@app/core/services/home.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-create-account',
  templateUrl: './create-account.component.html',
  styleUrls: ['./create-account.component.css']
})
export class CreateAccountComponent implements OnInit {

  public subscription: Subscription;
  public accountForm: FormGroup;
  public accTypeList: string[] = ['KBZ', 'AYA', 'CB', 'Yoma'];
  public submitted = false;
  public isFee:boolean=false;
  public isModal:boolean=false;

  @Input() editData: any;
  @Output() onSave: any = new EventEmitter<any>();

  constructor(private formBuilder: FormBuilder, private _service: HomeService) { }

  ngOnInit(): void {
    this.isModal=true;
    this.accountForm = this.formBuilder.group({
      name: ["", [Validators.required]],
      currency: ["MMK", [Validators.required]],
      amount: [null, [Validators.required]],
      accountType: ["", [Validators.required]],

    }
    );
    if (this.editData)
      this.getDetail();
  }

  getDetail() {
    this.accountForm = this.formBuilder.group({
      name: [this.editData.name, [Validators.required]],
      currency: [this.editData.currency, [Validators.required]],
      amount: [this.editData.amount, [Validators.required]],
      accountType: [this.editData.accountType, [Validators.required]],
    }
    );
  }
  onSubmit() {
    console.log(this.accountForm.invalid);
    this.submitted = true;
    if (this.accountForm.invalid) return;
    else {
      this.isModal=false;
      let finalData = this.getFinalData();
      if (this.editData) {
        this.onEdit(finalData);
      }
      else
        this.onCreate(finalData);

    }
  }

  onEdit(data) {
    this.subscription = this._service.editPayment(data, this.editData.accountId, "accounts")
      .subscribe((res) => {
   
        
        this.onSave.emit(true);
      })
  }

  onCreate(data) {
    this.subscription = this._service.createPayment(data, "accounts")
      .subscribe((res) => {
        this.onSave.emit(true);
      })
  }

  getFinalData() {
    let tempData: any = {};
    let formvalue = this.accountForm.value;
    tempData.name = formvalue.name;
    tempData.amount = formvalue.amount;
    tempData.currency = formvalue.currency;
    tempData.accountType = formvalue.accountType;
    tempData.fee=this.isFee;
    return tempData;

  }

  onCancel() {
    this.onSave.emit(true);
  }

  get f() { return this.accountForm.controls }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
